//Modules & Components
import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams } from 'ionic-angular';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { ClientQaRequestPage } from '../client-qa-request/client-qa-request';
import { ResetPasswordPage } from '../reset-password/reset-password';

@Component({
  selector: 'page-question-request',
  templateUrl: 'question-request.html'
})
export class QuestionRequestPage {

  public pageTitle: string;
  public userSecurityData;
  public questionRequestData;
  private typeRequest: number;

  user = {
    username: ''
  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider
  ) {
    this.typeRequest = navParams.get('typeRequest');
    switch (this.typeRequest) {
      case 0: this.pageTitle = 'Recuperar Contraseña'; break;
      case 1: this.pageTitle = 'Modificar Pregunta Secreta'; break;
      default: this.pageTitle = '';
    }
  }

  validate() {
    if (this.user.username != '') {
      this.userSecurityChangeRequest();
    } else {
      this.displayMessage("Error", "Introduzca un nombre de usuario")
    }
  }

  userSecurityChangeRequest() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.userSecurityChangeRequest(this.user.username.toLowerCase())
        .then(data => {
          this.userSecurityData = data;
          if (this.userSecurityData._body == null && this.userSecurityData.name == null && parseInt(this.userSecurityData.cod_respuesta) == 0) {
            loader.dismiss();
            this.questionRequest();
          } else {
            if (this.userSecurityData._body == null && this.userSecurityData.name == null) {
              this.displayMessage("Error", this.userSecurityData.cod_respuesta + ' - ' + this.userSecurityData.descripcion_respuesta)
            }
            loader.dismiss();
          }
        });
    });
  }

  questionRequest() {
    var questionRequestObject = {
      username: this.user.username.toLowerCase(),
      canal: "04",
      fechahora_disp: new Date()
    }

    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.questionRequest(questionRequestObject)
        .then(data => {
          this.questionRequestData = data;
          if (this.questionRequestData._body == null && this.questionRequestData.name == null && this.questionRequestData.cod_respuesta == "000") {
            loader.dismiss();
            switch (this.typeRequest) {
              case 0: this.navCtrl.push(ResetPasswordPage, this.questionRequestData); break;
              case 1: this.navCtrl.push(ClientQaRequestPage, this.questionRequestData); break;
            }
          } else {
            if (this.questionRequestData._body == null && this.questionRequestData.name == null) {
              this.displayMessage("Error", this.questionRequestData.cod_respuesta + ' - ' + this.questionRequestData.descripcion_respuesta)
            }
            loader.dismiss()
          }
        });
    });
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }
}



