//Modules & Components
import { Component } from '@angular/core';
import * as CryptoJS from 'crypto-js';
import { AlertController, LoadingController, NavController, NavParams, Platform, ToastController } from 'ionic-angular';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { ChangePasswordPage } from '../change-password/change-password';
import { ClientQaRequestPage } from '../client-qa-request/client-qa-request';
import { ClientidRequestPage } from '../clientid-request/clientid-request';
import { OptionsPage } from '../options/options';
import { QuestionRequestPage } from '../question-request/question-request';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public loginResponse: any;
  public formLoginData = {
    username: "",
    password: ""
  }
  username = "";
  password = "";
  isenabled: boolean = false;

  constructor(
    public navCtrl: NavController,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider,
    public alertCtrl: AlertController,
    public navParams: NavParams,
    private platform: Platform,
    public toastCtrl: ToastController
  ) {
    this.enableButtons(true);
    if (navParams.get("affiliationMsg")) {
      let toast = this.toastCtrl.create({
        message: 'Usuario afiliado exitosamente',
        showCloseButton: true
      });
      toast.present();
    }
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

  validate() {
    if (this.formLoginData.username != '' && this.formLoginData.password != '') {
      this.tokenRequest();
    } else {
      this.displayMessage('Error', 'Complete todos los campos correctamente');
    }
  }

  tokenRequest() {
    this.enableButtons(false);
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.tokenRequest(this.formLoginData.username.toLowerCase(), this.formLoginData.password)
        .then(data => {
          let res: any = data;
          this.username = this.formLoginData.username.toLowerCase();
          this.password = this.formLoginData.password;

          if (res._body != null || res.name != null) {
            loader.dismiss();
          } else {
            loader.dismiss();
            this.loginInApi();
            this.username = this.formLoginData.username.toLowerCase();
            this.password = this.formLoginData.password;
          }
        });
      this.enableButtons(true);

    });
  }

  loginInApi() {
    this.enableButtons(false);
    var loginObject = {
      username: this.username,
      password: CryptoJS.SHA512(this.password).toString(),
      canal: "04",
      fechahora_disp: new Date(),
      version: this.p2pServiceProvider.appVersion
    }
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });
    loader.present().then(() => {
      this.p2pServiceProvider.loginRequest(loginObject)
        .then(data => {
          this.loginResponse = data;

          if (this.loginResponse._body != null || this.loginResponse.name != null) {
            loader.dismiss(); // Otros errores
          } else {
            loader.dismiss();
            this.clearForm();
            this.p2pServiceProvider.loginData = data;
            this.enableButtons(true);

            if (this.p2pServiceProvider.loginData.cod_respuesta == "107") {
              this.displayMessage("Aviso", this.p2pServiceProvider.loginData.descripcion_respuesta)
              let loginData: any = data;
              loginData.isLoginCall = true;
              this.navCtrl.push(ChangePasswordPage, {
                loginData: loginData,
                password: CryptoJS.SHA512(this.password).toString(),
                username: this.username
              });
            }
            else if (this.loginResponse.cod_respuesta == "900") {
              let alert = this.alertCtrl.create({
                title: "Atención",
                message: this.loginResponse.descripcion_respuesta,
                buttons: [
                  {
                    text: 'OK',
                    handler: () => {
                      this.platform.exitApp();
                    }
                  }
                ]
              });

              alert.present();
            }
            else if (this.loginResponse.cod_respuesta == "123") {
              this.displayMessage("Aviso", this.p2pServiceProvider.loginData.descripcion_respuesta);
              let loginData: any = data;
              loginData.usuario = this.username;
              loginData.isLoginCall = true;
              this.navCtrl.push(ClientQaRequestPage, loginData);
            } else {
              this.navCtrl.push(OptionsPage, {
                loginData: data,
                password: CryptoJS.SHA512(this.password).toString(),
                username: this.username
              });
            }
          }
        });
    });
  }

  goToResetPassword() {
    this.navCtrl.push(QuestionRequestPage, { typeRequest: 0 });
  }

  enableButtons(active) {
    if (active) {
      this.isenabled = true;
    } else {
      this.isenabled = false;
    }
  }

  clearForm() {
    this.formLoginData.username = '';
    this.formLoginData.password = '';
  }

  getViewAffiliation() {
    this.navCtrl.push(ClientidRequestPage);
  }

  goToChangeQA() {
    this.navCtrl.push(QuestionRequestPage, { typeRequest: 1 });
  }
}



