import { Component } from '@angular/core';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { AlertController, App, LoadingController, Platform } from 'ionic-angular';

import { HomePage } from '../pages/home/home';
import { OptionsPage } from '../pages/options/options';
import { P2PServiceProvider } from '../providers/p2p-service/p2p-service';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage: any = HomePage;

  public pages: Array<{ title: string, component: any, icon: string }>;
  idleState = 'Not started.';
  timedOut = false;
  lastPing?: Date = null;
  backLogoutActive: boolean = false;

  constructor(
    private platform: Platform,
    private statusBar: StatusBar,
    private splashScreen: SplashScreen,
    private app: App,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private p2pServiceProvider: P2PServiceProvider) {

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();

      window.addEventListener('keyboardWillShow', (e) => {
          document.body.classList.add('keyboard-is-open');
      });

      window.addEventListener('keyboardWillHide', () => {
          document.body.classList.remove('keyboard-is-open');
      });

      platform.registerBackButtonAction(() => {
        if(this.p2pServiceProvider.onViewMsgPresent) {
          //DO NOTHING - VIEW MESSAGE PRESENT
        }
        else if (this.app.getActiveNav().getActive().instance instanceof OptionsPage) {

          if(this.backLogoutActive == false) {
            let alert = this.alertCtrl.create({
              title: "Salir",
              message: "¿Desea salir de la aplicación?",
              enableBackdropDismiss: false,
              buttons: [
                {
                  text: 'SI',
                  handler: () => {
                    //RE-VALID FOR ALERT PRESENT ON CHANGE NAV
                    if (this.app.getActiveNav().getActive().instance instanceof OptionsPage) {
                      this.logout();
                    }
                    else {
                      this.backLogoutActive = false;
                    }
                  }
                },
                {
                  text: 'NO',
                  handler: () => {
                    this.backLogoutActive = false;
                  }
                }
              ]
            });

            this.backLogoutActive = true;
            alert.present();
          }
        } else if (this.app.getActiveNav().getActive().instance instanceof HomePage) {
          this.platform.exitApp();
        } else {
          this.app.getActiveNav().pop();
        }
      });
    });
  }

  logout() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.logoutRequest()
        .then(data => {
          loader.dismiss();
          this.backLogoutActive = false;
          this.app.getActiveNav().popToRoot();
        });
    });
  }
}



