import { Component } from '@angular/core';
import { ViewController } from 'ionic-angular';

@Component({
  selector: 'page-affiliation-terms',
  templateUrl: 'affiliation-terms.html',
})
export class AffiliationTermsPage {

  constructor(public viewCtrl: ViewController) { }

  takeTermsAction(action: Number) {
    let resp = { id: action };
    this.viewCtrl.dismiss(resp);
  }
}



