//Modules & Components
import { Component } from '@angular/core';
import * as CryptoJS from 'crypto-js';
import { AlertController, LoadingController, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { HomePage } from '../home/home';

@Component({
  selector: 'page-change-password',
  templateUrl: 'change-password.html'
})
export class ChangePasswordPage {

  public username;
  public loginData;
  public changePasswordResult;

  public countDown;
  private counter = 300;

  change = {
    oldPassword: '',
    newPassword: '',
    newPasswordConfirm: '',
    securityCode: ''
  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public p2pServiceProvider: P2PServiceProvider,
    public loadingCtrl: LoadingController,
  ) {
    this.username = navParams.get('username');
    this.loginData = navParams.get('loginData');
  }

  ionViewDidLoad() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.userSecurityChangeRequest(this.username.toLowerCase())
        .then(data => {
          let userSecurityData: any = data;
          if (userSecurityData._body == null && userSecurityData.name == null && parseInt(userSecurityData.cod_respuesta) == 0) {
            loader.dismiss();
            this.counter = 300;
            this.countDown = Observable.timer(0, 1000).take(this.counter)
              .map(() => this.counter = this.counter == 0 ? 0 : (this.counter % 100) == 0 ? (this.counter - 41) : (this.counter - 1));
          } else {
            if (userSecurityData._body == null && userSecurityData.name == null) {
              this.displayMessage("Error", userSecurityData.cod_respuesta + ' - ' + userSecurityData.descripcion_respuesta)
            }
            loader.dismiss();
          }
        });
    });
  }

  validate() {
    if (this.change.oldPassword == '' || this.change.newPassword == '' || this.change.newPasswordConfirm == '') {
      this.displayMessage("Error", "Complete los campos correctamente.")
    } else if (this.change.securityCode.trim().length < 8) {
      this.displayMessage("Error", "El código de seguridad debe contener mínimo 8 letras y/o números");
    } else if (!this.change.securityCode.match('^[a-zA-Z0-9]*$')) {
      this.displayMessage("Error", "El código de seguridad debe solo contener letras y/o números");
    } else if (this.change.newPassword != this.change.newPasswordConfirm) {
      this.displayMessage("Error", "Las contraseñas no coinciden");
    } else if (this.p2pServiceProvider.validatePassword(this.username, this.loginData.nombre_afiliado, this.loginData.cedula,
      this.loginData.lista_palabras, this.loginData.expresion_regular, this.change.newPassword) == false) {
      let alert = this.alertCtrl.create({
        title: "Error",
        subTitle: "La nueva contraseña no cumple con los requisitos.",
        buttons: [{
          text: 'OK'
        }, {
          text: 'AYUDA',
          handler: () => {
            this.showPasswordHelp();
          }
        }]
      });
      alert.present();
    } else {
      this.changePasswordRequest()
    }
  }

  changePasswordRequest() {
    var changePasswordObject = {
      "username": this.username,
      "oldpassword": CryptoJS.SHA512(this.change.oldPassword).toString(),
      "newpassword": CryptoJS.SHA512(this.change.newPassword).toString(),
      "canal": "04", "otp": this.change.securityCode,
      "fechahora_disp": new Date()
    };

    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.changePasswordRequest(changePasswordObject)
        .then(data => {
          this.changePasswordResult = data;

          if (this.changePasswordResult._body == null && this.changePasswordResult.name == null && this.changePasswordResult.cod_respuesta == "000") {
            this.displayMessage("Aviso", "Operación Exitosa");
            this.logout();
          } else {
            if (this.changePasswordResult._body == null && this.changePasswordResult.name == null) {
              this.displayMessage("Error", (this.changePasswordResult.cod_respuesta) + ' - ' + this.changePasswordResult.descripcion_respuesta);

              if (this.changePasswordResult.cod_respuesta == this.p2pServiceProvider.SESSION_EXPIRED) {
                this.navCtrl.popToRoot();
              }
            }
          }
          loader.dismiss();
        });
    });
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

  logout() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.logoutRequest()
        .then(data => {
          loader.dismiss();
          this.navCtrl.setRoot(HomePage);
          this.navCtrl.popToRoot();
        });
    });
  }

  showPasswordHelp() {
    this.p2pServiceProvider.showPasswordHelp(this.loginData.lista_palabras);
  }
}



