import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams, Platform } from 'ionic-angular';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { PhoneChooserPage } from '../phone-chooser/phone-chooser';

@Component({
  selector: 'page-clientid-request',
  templateUrl: 'clientid-request.html'
})
export class ClientidRequestPage {
  private unregisterBackAction: any;
  public typesList: Array<string>;
  public id: Number;
  public idType: string;

  public phonesData: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public platform: Platform,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider) {
    this.docTypeList();
    this.navCtrl.swipeBackEnabled = false;
  }

  ionViewWillLeave() {
    console.log(typeof this.unregisterBackAction == 'function');
    if (this.unregisterBackAction && typeof this.unregisterBackAction == 'function') {
      this.unregisterBackAction();
    }
  }

  cancelAffiliation() {
    this.ionViewWillLeave();
    this.navCtrl.popToRoot();
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

  docTypeList() {
    let loader = this.loadingCtrl.create({
      content: "Espere por favor",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.getAffiliationDocTypes().then(data => {
        let res: any = data;

        if (res.cod_respuesta && !isNaN(res.cod_respuesta) && parseInt(res.cod_respuesta) == 0) {
          loader.dismiss();
          this.typesList = res.TiposAfiliados;
        }
        else {
          if (res.cod_respuesta && res.descripcion_respuesta) {
            this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta);
          }
          else {
            this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
          }
          loader.dismiss();
        }
      });
    });
  }

  validClientID() {
    //VALID DATA
    if (!this.idType || this.idType.toString() == "" || !this.id || this.id.toString().length < 6) {
      this.displayMessage("Error", "Debe ingresar un documento de identificación válido");
    }
    else if (isNaN(this.id.valueOf()) || this.id.toString().trim().length == 0) {
      this.displayMessage("Error", "Debe ingresar solo números en el número de documento");
    }
    else {
      let loader = this.loadingCtrl.create({
        content: "Procesando...",
      });

      this.p2pServiceProvider.onViewMsgPresent = true;
      loader.onDidDismiss(() => { this.p2pServiceProvider.onViewMsgPresent = false; });

      loader.present().then(() => {
        this.p2pServiceProvider.getClientPhones(this.id, this.idType).then(data => {
          let res: any = data;
          console.log("Valid res " + res + " with property " + res.Telefonos);
          if (res.cod_respuesta && !isNaN(res.cod_respuesta) && parseInt(res.cod_respuesta) == 0) {
            loader.dismiss();
            this.navCtrl.push(PhoneChooserPage, { clientId: this.id, clientIdType: this.idType, phones: res.Telefonos });
          }
          else {
            if (res.cod_respuesta && res.descripcion_respuesta) {
              this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta);
            }
            else {
              this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
            }
            loader.dismiss();
            this.cancelAffiliation();
          }
        });
      });
    }
  }
}



