//Modules & Components
import { Component } from '@angular/core';
import * as CryptoJS from 'crypto-js';
import { AlertController, LoadingController, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs/Rx';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { HomePage } from '../home/home';

@Component({
  selector: 'page-reset-password',
  templateUrl: 'reset-password.html'
})
export class ResetPasswordPage {
  public questionObject = {
    username: '',
    pregunta: '',
    expresion_regular: '',
    cedula: '',
    nombre_afiliado: '',
    lista_palabras: [{ palabra: '' }]
  };

  reset = {
    codigo: '',
    newPasswordConfirm: '',
    newPassword: '',
    respuesta: ''
  }

  public resetRequestData;
  public countDown;
  private counter = 300;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider,
  ) {
    this.questionObject.username = navParams.get('usuario');
    this.questionObject.pregunta = navParams.get('pregunta');
    this.questionObject.expresion_regular = navParams.get('expresion_regular');
    this.questionObject.cedula = navParams.get('cedula');
    this.questionObject.nombre_afiliado = navParams.get('nombre_afiliado');
    this.questionObject.lista_palabras = navParams.get('lista_palabras');
  }

  ionViewDidLoad() {
    this.countDown = Observable.timer(0, 1000).take(this.counter)
      .map(() => this.counter = this.counter == 0 ? 0 : (this.counter % 100) == 0 ? (this.counter - 41) : (this.counter - 1));
  }

  showPasswordHelp() {
    this.p2pServiceProvider.showPasswordHelp(this.questionObject.lista_palabras);
  }

  validate() {
    if (this.reset.newPassword == '' || this.reset.newPasswordConfirm == '' && this.reset.respuesta == '') {
      this.displayMessage("Error", "Complete todos los campos correctamente.");
    } else if (this.reset.codigo.trim().length < 8) {
      this.displayMessage("Error", "El código de seguridad debe contener mínimo 8 letras y/o números");
    } else if (!this.reset.codigo.match('^[a-zA-Z0-9]*$')) {
      this.displayMessage("Error", "El código de seguridad debe solo contener letras y/o números");
    } else if (this.reset.newPassword != this.reset.newPasswordConfirm) {
      this.displayMessage("Error", "Las contraseñas deben ser iguales.");
    } else if (this.p2pServiceProvider.validatePassword(this.questionObject.username, this.questionObject.nombre_afiliado,
      this.questionObject.cedula, this.questionObject.lista_palabras, this.questionObject.expresion_regular,
      this.reset.newPassword) == false) {
      let alert = this.alertCtrl.create({
        title: "Error",
        subTitle: "La nueva contraseña no cumple con los requisitos.",
        buttons: [{
          text: 'OK'
        }, {
          text: 'AYUDA',
          handler: () => {
            this.showPasswordHelp();
          }
        }]
      });
      alert.present();
    } else {
      this.resetRequest()
    }
  }

  resetRequest() {
    var resetRequestObject = {
      username: this.questionObject.username,
      newpassword: CryptoJS.SHA512(this.reset.newPassword).toString(),
      respuesta: CryptoJS.SHA512(this.reset.respuesta).toString().toUpperCase(),
      canal: "04", otp: this.reset.codigo,
      fechahora_disp: new Date()
    }

    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.resetPasswordRequest(resetRequestObject)
        .then(data => {
          this.resetRequestData = data;

          if (this.resetRequestData._body == null && this.resetRequestData.name == null && this.resetRequestData.cod_respuesta == "000") {
            this.displayMessage("Aviso", "Operación Exitosa");
            loader.dismiss();
            this.navCtrl.setRoot(HomePage);
            this.navCtrl.popToRoot();
          }
          else {
            if (this.resetRequestData._body == null && this.resetRequestData.name == null) {
              this.displayMessage("Error", this.resetRequestData.cod_respuesta + ' - ' + this.resetRequestData.descripcion_respuesta);
            }
            loader.dismiss();
          }
        });
    });

  }

  resendValidCode() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.userSecurityChangeRequest(this.questionObject.username.toLowerCase())
        .then(data => {
          let userSecurityData: any = data;
          if (userSecurityData._body == null && userSecurityData.name == null && parseInt(userSecurityData.cod_respuesta) == 0) {
            loader.dismiss();
            this.counter = 300;
            this.ionViewDidLoad();
          } else {
            if (userSecurityData._body == null && userSecurityData.name == null) {
              this.displayMessage("Error", userSecurityData.cod_respuesta + ' - ' + userSecurityData.descripcion_respuesta)
            }
            loader.dismiss();
          }
        });
    });
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }
}



