import { Component } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular/components/alert/alert-controller';
import { LoadingController } from 'ionic-angular/components/loading/loading-controller';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { VerifyAffiliationPage } from '../verify-affiliation/verify-affiliation';

@Component({
  selector: 'page-phone-chooser',
  templateUrl: 'phone-chooser.html',
})
export class PhoneChooserPage {

  private clientID: string;
  private clientIdType: string;
  public chooseForm: FormGroup;
  public phones: Object;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private formBuilder: FormBuilder,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider,
    public alertCtrl: AlertController) {

    this.clientID = navParams.get('clientId');
    this.clientIdType = navParams.get('clientIdType');
    this.chooseForm = this.formBuilder.group({ phoneChoose: '' });
    this.phones = this.navParams.get("phones");
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['Cerrar']
    });
    alert.present();
  }

  cancelAffiliation() {
    this.navCtrl.popToRoot();
  }

  notifyPhoneChoose() {
    if (this.chooseForm.get('phoneChoose').value.length == 0) {
      this.displayMessage("Error", "Debe seleccionar un teléfono");
    }
    else {
      let loader = this.loadingCtrl.create({
        content: "Procesando...",
      });

      loader.present().then(() => {
        this.p2pServiceProvider.getAffiliationCode(this.clientIdType, this.clientID, this.chooseForm.get('phoneChoose').value)
          .then(data => {
            let res: any = data;

            if (res.cod_respuesta && !isNaN(res.cod_respuesta) && parseInt(res.cod_respuesta) == 0) {
              loader.dismiss();
              this.navCtrl.push(VerifyAffiliationPage, {
                clientId: this.clientID, clientIdType: this.clientIdType,
                clientPhone: this.chooseForm.get('phoneChoose').value
              });
            }
            else {
              if (res.cod_respuesta && res.descripcion_respuesta) {
                this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta);
              }
              else {
                this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
              }
              loader.dismiss();
            }
          });
      });
    }
  }
}



