  tokenRequest(user, password) {
    this.user = user;
    this.password = CryptoJS.SHA512(password).toString();
    let myHeaders: HttpHeaders = new HttpHeaders;

    myHeaders.set('Accept', 'application/json; charset=utf-8');
    myHeaders.append('Content-Type', 'application/x-www-form-urlencoded');

    let opt = {
      headers: myHeaders
    }

    return new Promise((resolve, reject) => {
      var body = 'grant_type=password&username=' + user + '&password=' + this.password;
      this.http.post(this.baseUrl + '/Token', body, opt)
        .timeout(this.timeout)
        .subscribe(data => {
          this.data = data;
          this.token = data;
          resolve(this.data);
        }, (errorResponse: any) => {
          if (errorResponse.status == 0) {
            this.displayMessage("Error", "Fallo en la conexión")
          } else if (errorResponse.name && (errorResponse.name == "TimeoutError")) {
            this.displayMessage("Error", "Fallo en la conexión, tiempo límite excedido.")
          } else {
            try {
              this.displayMessage("Error", JSON.parse(errorResponse._body).Message)
            } catch (e) {
              this.displayMessage("Error", "Ha ocurrido un error, intente nuevamente")
            }
          }
          resolve(errorResponse)
        });
    });
  }

