import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs/Rx';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { AffiliationPage } from '../affiliation/affiliation';

@Component({
  selector: 'page-verify-affiliation',
  templateUrl: 'verify-affiliation.html'
})
export class VerifyAffiliationPage {

  private clientInfo: any;
  private idAccount: Number;
  private validCode: string;

  countDown;
  counter = 300;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public p2pServiceProvider: P2PServiceProvider,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController) {

    this.clientInfo = {
      docType: navParams.get('clientIdType'),
      docNumber: navParams.get('clientId'),
      phone: navParams.get('clientPhone'),
      id: '',
      name: '',
      email: '',
      accountLast: '',
      accounts: [],
      regexPsw: "",
      forbidden: [],
      validationKey: ''
    };
  }

  ionViewDidLoad() {
    this.countDown = Observable.timer(0, 1000).take(this.counter)
      .map(() => this.counter = this.counter == 0 ? 0 : (this.counter % 100) == 0 ? (this.counter - 41) : (this.counter - 1));
  }

  cancelAffiliation() {
    this.navCtrl.popToRoot();
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['Cerrar']
    });
    alert.present();
  }

  sendVerificationData() {
    if (this.validCode && this.idAccount && this.validCode.length >= 8 && this.idAccount.toString().trim().length == 4 &&
      !isNaN(this.idAccount.valueOf()) && this.idAccount.toString() != '0000' && this.validCode.match('^[a-zA-Z0-9]*$')) {

      this.clientInfo.accountLast = this.idAccount.toString();
      this.clientInfo.validationKey = this.validCode.toUpperCase();

      let loader = this.loadingCtrl.create({
        content: "Procesando...",
      });

      loader.present().then(() => {
        this.p2pServiceProvider.validAffiliationCode(this.clientInfo, this.validCode)
          .then(data => {
            let res: any = data;

            if (res.cod_respuesta && !isNaN(res.cod_respuesta) && parseInt(res.cod_respuesta) == 0) {
              this.clientInfo.id = res.nro_cliente;
              this.clientInfo.name = res.nombre;
              this.clientInfo.email = res.email;
              this.clientInfo.forbidden = res.PalabrasRestringidas;
              this.clientInfo.regexPsw = res.expresion_regular;

              let accountList = [{ id: res.cuentas[0] }];
              let accountID = 1;

              while (accountID < res.cuentas.length) {
                accountList.push({ id: res.cuentas[accountID] });
                accountID++;
              }

              this.clientInfo.accounts = accountList;

              loader.dismiss();
              this.navCtrl.push(AffiliationPage, { clientInfo: this.clientInfo });
            }
            else {
              if (res.cod_respuesta && res.descripcion_respuesta) {
                this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta);
              }
              else {
                this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
              }
              loader.dismiss();
            }
          });
      });
    }
    else if (this.idAccount && (isNaN(this.idAccount.valueOf()) || this.idAccount.toString() == '0000')) {
      this.displayMessage("Error", "Debe ingresar un código de cuenta valido.");
    }
    else if (this.validCode && !(this.validCode.match('^[a-zA-Z0-9]*$'))) {
      this.displayMessage("Error", "El código de validación debe solo contener letras y/o números.");
    }
    else {
      this.displayMessage("Error", "Debe completar los datos correctamente");
    }
  }

  resendValidCode() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.getAffiliationCode(this.clientInfo.docType, this.clientInfo.docNumber, this.clientInfo.phone)
        .then(data => {
          let res: any = data;

          if (res.cod_respuesta && !isNaN(res.cod_respuesta) && parseInt(res.cod_respuesta) == 0) {
            loader.dismiss();
            this.counter = 300;
            this.ionViewDidLoad();
          }
          else {
            if (res.cod_respuesta && res.descripcion_respuesta) {
              this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta);
            }
            else {
              this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
            }
            loader.dismiss();
          }
        });
    });
  }
}



