import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams } from 'ionic-angular';

import { DateviewPipe } from '../../pipes/dateview/dateview';
import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';

interface PaymentInfo{
  id: number,
  bank: string,
  bankCode: string,
  amount: any,
  formatAmount: string,
  phone: string,
  concept: string,
  tranDate: string,
  traceQuantum: string,
  switchCode: string
}

@Component({
  selector: 'page-company-received',
  templateUrl: 'company-received.html',
  providers: [DateviewPipe]
})
export class CompanyReceivedPage {
  public onGetMore: boolean = false;
  public noResult: boolean = false;
  public viewFilter: number = 0;

  public loginData: any;
  public payments = [] as Array<PaymentInfo>;

  public beginDate: string;
  public topDate: string;
  public phonecode: string;
  public phone: string;

  constructor(
    public alertCtrl: AlertController,
    private dateView: DateviewPipe,
    public loadingCtrl: LoadingController,
    public navCtrl: NavController,
    public navParams: NavParams,
    public p2pServiceProvider: P2PServiceProvider) {
    this.loginData = this.navParams.get('loginData');
  }

  ionViewDidLoad() { this.getReceivedPayments(null, null, 0); }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['CERRAR']
    });
    alert.present();
  }

  formatAmount(amount) {
    let formatRes = amount.toString().replace(/\./gi, ",").replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    return formatRes.indexOf(',') < 0 ? formatRes + ',00' : formatRes;
  }

  getBankName(code: string) {
    let bank = this.loginData.lista_bancos.filter(item => item.codigo == code);
    return bank == null || bank.length == 0 ? '' : bank[0].nombre;
  }

  maskPhone(phone) {
    if (phone.length > 10) {
      phone = phone.substring(0, 4) + 'XXX' + phone.substring(7, phone.length + 1);
    }
    return phone;
  }

  getDividerFormat(date: string) {console.log("Date reference: " + date);
    if(parseInt(date) >= 1300) {
      date = (parseInt(date) - 1300).toString();
      switch(date.length) {
        case 3: return date.substring(0,1) +':'+ date.substring(1,3) + ' pm';
        case 4: return date.substring(0,2) +':'+ date.substring(2,4) + ' pm';
      }
    }

    switch(date.length) {
      case 3: return date.substring(0,1) +':'+ date.substring(1,3) + ' am';
      case 4: return date.substring(0,2) +':'+ date.substring(2,4) + ' am';
    }
  }

  filterByDate() {
    let minDate = this.beginDate.substring(this.beginDate.indexOf('T')+1, this.beginDate.indexOf('T')+6);
    let maxDate = this.topDate.substring(this.topDate.indexOf('T')+1, this.topDate.indexOf('T')+6);

    minDate = minDate.replace(':', '');
    maxDate = maxDate.replace(':', '');

    if(parseInt(minDate) > parseInt(maxDate)) {
      this.displayMessage('Error', 'La hora hasta debe ser mayor a la hora desde.');
    }
    else {
      this.onGetMore = false;
      this.getReceivedPayments(minDate, maxDate, 0);
    }
  }

  filterByPhone() {console.log("Filter me by Phone");
    if(this.phonecode == undefined || this.phonecode.length == 0) {
      this.displayMessage('Error', 'Debe seleccionar un prefijo válido.');
    }
    else if(this.phone  == undefined || this.phone.toString().trim().length < 7 || (new RegExp(/^[0-9]+$/gi).test(this.phone.toString())) == false) {
      this.displayMessage("Error", "El número de teléfono debe contener mínimo 7 dígitos numéricos");
    }
    else {console.log("Aquí vamos a consultar con el teléfono");
      this.onGetMore = false;
      this.getPhoneReceivedPayments(0);
    }
  }

  detailView(index: number) {
    let alert = this.alertCtrl.create({
      title: 'Detalle del Pago',
      message:'<b>Transacción Exitosa</b><hr/>' +
              '<b id="dateHead">Banco:</b> ' + this.payments[index].bank + '<br/>' +
              '<b id="dateHead">Fecha:</b> ' + this.dateView.transform(this.payments[index].tranDate, 'DATETIME') + '<br/>' +
              '<b id="dateHead">Monto:</b> ' + this.payments[index].formatAmount + ' Bs.<br/>' +
              '<b id="dateHead">Concepto:</b> ' + this.payments[index].concept + '<br/>' +
              '<b id="dateHead">Ref. Quantum:</b> ' + this.payments[index].traceQuantum + '<br/>' +
              '<b id="dateHead">Ref. Suiche:</b> ' + this.payments[index].switchCode,
      buttons: ['CERRAR']
    });
    alert.present();
  }

  getPayments(id: number) {
    if(this.viewFilter != id) {
      this.viewFilter = id;

      switch(id) {
        case 0: this.onGetMore = false; this.getReceivedPayments(null, null, 0); break;
        case 1: let thisMoment = new Date();
                let hourFormat = thisMoment.getHours() < 10 ? '0' + thisMoment.getHours() : thisMoment.getHours().toString();
                hourFormat += ":" + thisMoment.getMinutes();

                this.beginDate = hourFormat;

                let nextHour = new Date(thisMoment.getTime() + 3600000);
                hourFormat = nextHour.getHours() < 10 ? '0' + nextHour.getHours() : nextHour.getHours().toString();
                hourFormat += ":" + thisMoment.getMinutes();

                this.topDate = hourFormat;
                break;
        case 2: this.phonecode = ''; this.phone = ''; break;
      }
    }
  }

  getMorePayments(reference: number) {
    this.onGetMore = true;

    switch(this.viewFilter) {
      case 0: this.getReceivedPayments(null, null, reference); break;
      case 1: let minDate = this.beginDate.substring(this.beginDate.indexOf('T')+1, this.beginDate.indexOf('T')+6);
              let maxDate = this.topDate.substring(this.topDate.indexOf('T')+1, this.topDate.indexOf('T')+6);
              this.getReceivedPayments(minDate.replace(':', ''), maxDate.replace(':', ''), reference); break;
      case 2: this.getPhoneReceivedPayments(reference); break;
    }
  }


  /***** SERVICES *****/
  getReceivedPayments(minDate: string, maxDate: string, reference: number) {
    let response: any;
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    this.p2pServiceProvider.onViewMsgPresent = true;
    loader.onDidDismiss(() => { this.p2pServiceProvider.onViewMsgPresent = false; });
    loader.present();

    this.p2pServiceProvider.companyTranBalanceRequest(minDate, maxDate, reference)
        .then(data => {
          loader.dismiss();
          response = data;
          if(this.onGetMore == false)this.payments = [] as Array<PaymentInfo>;

          if(response && parseInt(response.cod_respuesta) == 0) {
            if(response.lista_mov && response.lista_mov.length > 0) {
              this.noResult = false;

              for(let data of response.lista_mov) {
                let element: PaymentInfo = {
                  id: data.id,
                  bank: this.getBankName(data.codigo_banco),
                  bankCode: data.codigo_banco,
                  amount: data.monto,
                  formatAmount: this.formatAmount(data.monto),
                  phone: data.telefono,
                  concept: data.concepto,
                  tranDate: data.fecha_tran,
                  traceQuantum: data.trace_quantum,
                  switchCode: data.codigo_suiche
                }

                this.payments.push(element);
              }
            }
            else { this.noResult = true; }
          }
          else if(response.cod_respuesta && response.descripcion_respuesta) {
            this.noResult = true;
            this.displayMessage("Error", response.cod_respuesta + ' - ' + response.descripcion_respuesta);
          }
          else {
            this.noResult = true;
            this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
          }
    });
  }

  getPhoneReceivedPayments(reference: number) {
    let response: any;
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    this.p2pServiceProvider.onViewMsgPresent = true;
    loader.onDidDismiss(() => { this.p2pServiceProvider.onViewMsgPresent = false; });
    loader.present();

    this.p2pServiceProvider.companyPhoneBalanceRequest(this.phonecode + this.phone, reference)
        .then(data => {
          loader.dismiss();
          response = data;

          console.log("Active getMore ? " + this.onGetMore);

          if(this.onGetMore == false)this.payments = [] as Array<PaymentInfo>;

          if(response && parseInt(response.cod_respuesta) == 0) {
            if(response.lista_mov && response.lista_mov.length > 0) {
              this.noResult = false;

              for(let data of response.lista_mov) {
                let element: PaymentInfo = {
                  id: data.id,
                  bank: this.getBankName(data.codigo_banco),
                  bankCode: data.codigo_banco,
                  amount: data.monto,
                  formatAmount: this.formatAmount(data.monto),
                  phone: data.telefono,
                  concept: data.concepto,
                  tranDate: data.fecha_tran,
                  traceQuantum: data.trace_quantum,
                  switchCode: data.codigo_suiche
                }

                this.payments.push(element);
              }
            }
            else { this.noResult = true; }
          }
          else if(response.cod_respuesta && response.descripcion_respuesta) {
            this.noResult = true;
            this.displayMessage("Error", response.cod_respuesta + ' - ' + response.descripcion_respuesta);
          }
          else {
            this.noResult = true;
            this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
          }
    });
  }

}



