import { DatePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'dateview',
})
export class DateviewPipe implements PipeTransform {
  transform(value: string, dateType: string): string {
    if (dateType != undefined && (dateType.toUpperCase() == 'DATETIME' || dateType.toUpperCase() == 'TIME')) {
      let hourType: string = ' AM';
      let showOnlyHour: boolean = (dateType.toUpperCase() == 'TIME');

      let time = value.substring(value.indexOf("T") + 1, value.length);
      let hourFraction = time.substring(0, 2);

      if (parseInt(hourFraction) > 12) {
        hourFraction = (parseInt(hourFraction) - 12).toString();
        if (hourFraction.length == 1) hourFraction = '0' + hourFraction;
        hourType = ' PM';
      }
      else if (parseInt(hourFraction) == 12) { hourType = ' PM'; }

      hourFraction = hourFraction == '00' ? '12' : hourFraction;

      return (showOnlyHour ? '' : ((new DatePipe('es')).transform(value, 'dd/MM/yyyy')) + ' ') + hourFraction + time.substring(2, 5) + hourType;
    }
    else { return (new DatePipe('es')).transform(value, 'dd/MM/yyyy'); }
  }
}



