//Modules & Components
import { Component } from '@angular/core';
import { AlertController, LoadingController, ModalController, NavController, NavParams } from 'ionic-angular';

import { HomePage } from '../../pages/home/home';
import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { FrecuentSelectionPage } from '../frecuent-selection/frecuent-selection';
import { OrderDetailPage } from '../order-detail/order-detail';

@Component({
  selector: 'page-payment-order',
  templateUrl: 'payment-order.html'
})
export class PaymentOrderPage {

  public username: any;
  public loginData: any;

  order = {
    bankName: '',
    code: '',
    phone: '',
    idType: '',
    idNumber: '',
    amount: '',
    concept: ''
  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public modalCtrl: ModalController,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider
  ) {
    this.username = navParams.get('username');
    this.loginData = navParams.get('loginData');
  }

  validate() {

    if (this.order.amount.trim().length == 0 || this.order.amount == "0,00") {
      this.displayMessage("Error", "Debe ingresar un monto válido mayor a cero");
    }
    else if (this.order.amount.trim() != "" && this.order.bankName != "" && this.order.code != ""
      && this.order.idNumber != "" && this.order.idType != "" && this.order.phone != ""
      && (new RegExp(/^\d{1,3}[\.(?=\d{3})]*,\d{1,2}/g)).test(this.order.amount) == true
      && !isNaN(Number(this.order.amount.replace(/\./g, "").replace(",", ".")))) {
      this.logForm();
    } else {
      this.displayMessage("Error", "Complete los campos correctamente.");
    }
  }
  logForm() {
    this.goToOrderDetail(this.order);
  }

  goToOrderDetail(order) {
    let modal = this.modalCtrl.create(OrderDetailPage, {
      bankName: order.bankName,
      code: order.code,
      phone: order.phone,
      idType: order.idType,
      idNumber: order.idNumber,
      amount: Number(order.amount.replace(/\./g, "").replace(",", ".")).toFixed(2),
      concept: order.concept,
      username: this.username,
      loginData: this.loginData,
    });
    modal.present();
    modal.onDidDismiss(data => {
      if (data) {
        if (data.status == true && data.response.cod_respuesta == "000") {
          let summary = "<p><b>Banco:</b> " + order.bankName + "</p>"
            + "<p><b>Teléfono:</b> " + order.code + "-" + order.phone + "</p>"
            + "<p><b>Documento:</b> " + order.idType + "-" + order.idNumber + "</p>"
            + "<p><b>Monto:</b> " + order.amount + "</p>"
            + "<p><b>Referencia:</b> " + data.response.cod_aprobacion + "</p>";

          this.p2pServiceProvider.accountInfo.balance = this.formatAmount(Number(data.response.saldo_disponible).toFixed(2));
          this.p2pServiceProvider.accountInfo.dailyLimit = this.formatAmount(Number(this.loginData.limite_diario - data.response.acu_diario).toFixed(2));
          this.p2pServiceProvider.accountInfo.monthLimit = this.formatAmount(Number(this.loginData.limite_mensual - data.response.acu_mensual).toFixed(2));

          let alert = this.alertCtrl.create({
            title: "Pago Exitoso",
            message: summary,
            buttons: [
              {
                text: 'Cerrar Sesión',
                handler: () => {
                  this.logout();
                }
              },
              {
                text: 'Continuar',
                handler: () => {
                  this.navCtrl.pop();
                }
              }
            ]
          });
          alert.present();
        } else if (data.status == true && data.response.cod_respuesta != "000" && data.response.name == null) {
          this.displayMessage("Error", data.response.cod_respuesta + ' - ' + data.response.descripcion_respuesta);

          if (data.response.ind_act && data.response.ind_act == 0) {
            this.p2pServiceProvider.accountInfo.balance = this.formatAmount(Number(data.response.saldo_disponible).toFixed(2));
          }

          if (data.response.cod_respuesta == this.p2pServiceProvider.SESSION_EXPIRED) {
            this.navCtrl.popToRoot();
          }
        }
      }
    });
  }

  displayMessage(title, subtitle) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subtitle,
      buttons: ['OK']
    });
    alert.present();
  }

  formatAmount(number) {
    return number.replace(/\./gi, ",").replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  getFrecuentView() {
    let modal = this.modalCtrl.create(FrecuentSelectionPage);
    modal.onDidDismiss(data => {
      let frecuentData: any = data;

      if (frecuentData && frecuentData.bank) {
        //HAS DATA => LOAD FRECUENT PAYMENT DATA
        this.order.bankName = frecuentData.bank;
        this.order.idType = frecuentData.documentType;
        this.order.idNumber = frecuentData.documentID;
        this.order.code = frecuentData.phoneCode;
        this.order.phone = frecuentData.phone;
      }
      else if (frecuentData && frecuentData.cod_respuesta && frecuentData.cod_respuesta == this.p2pServiceProvider.SESSION_EXPIRED) {
        this.navCtrl.popToRoot();
      }
    });
    modal.present();
  }

  logout() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.logoutRequest()
        .then(data => {
          loader.dismiss();
          this.navCtrl.setRoot(HomePage);
          this.navCtrl.popToRoot();
        });
    });
  }
}



