import 'rxjs/add/observable/interval';

import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';

@Component({
  selector: 'page-company-balance',
  templateUrl: 'company-balance.html',
})
export class CompanyBalancePage {
  public accountBalance: string;
  public isFreezeTime: boolean;

  constructor(
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public navCtrl: NavController,
    public navParams: NavParams,
    public p2pServiceProvider: P2PServiceProvider) {
    this.accountBalance = "0,00";
  }

  ionViewDidLoad() { this.getCompanyBalance(); }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['CERRAR']
    });
    alert.present();
  }

  getCompanyBalance() {
    this.isFreezeTime = true;
    Observable.interval(11000).subscribe(() => this.isFreezeTime = false);

    let response: any;
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    this.p2pServiceProvider.onViewMsgPresent = true;
    loader.onDidDismiss(() => { this.p2pServiceProvider.onViewMsgPresent = false; });
    loader.present();

    this.p2pServiceProvider
      .companyBalanceRequest()
      .then(data => {
        loader.dismiss();
        response = data;

        if (response && parseInt(response.cod_respuesta) == 0) {
          if (response.monto != undefined) this.accountBalance = this.formatAmount(response.monto.toString());
        }
        else if (response.cod_respuesta && response.descripcion_respuesta) {
          this.displayMessage("Error", response.cod_respuesta + ' - ' + response.descripcion_respuesta);
        }
        else {
          this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
        }
      });
  }

  formatAmount(amount) {
    let formatRes = amount.toString().replace(/\./gi, ",").replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    return formatRes.indexOf(',') < 0 ? formatRes + ',00' : formatRes;
  }
}



