import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams, ViewController } from 'ionic-angular';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';

@Component({
  selector: 'page-order-detail',
  templateUrl: 'order-detail.html'
})
export class OrderDetailPage {

  public username: any;
  public loginData: any;
  public bankName: any;
  public code: any;
  public phone: any;
  public idType: any;
  public idNumber: any;
  public amount: any;
  public concept: any;
  public p2pResponse: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCrtl: ViewController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public p2pServiceProvider: P2PServiceProvider
  ) {
    this.bankName = this.navParams.get('bankName');
    this.code = this.navParams.get('code');
    this.phone = this.navParams.get('phone');
    this.idType = this.navParams.get('idType');
    this.idNumber = this.navParams.get('idNumber');
    this.amount = this.navParams.get('amount');
    if (this.navParams.get('concept') != null) {
      this.concept = this.navParams.get('concept');
    }
    this.username = navParams.get('username');
    this.loginData = navParams.get('loginData');
  }


  close() {
    this.viewCrtl.dismiss({
      status: false
    });
  }

  getBankCode(bankName) {
    var code = ""
    for (let i of this.loginData.lista_bancos) {
      if (i.nombre == bankName) {
        code = i.codigo;
      }
    }
    return code;
  }

  paymentRequest() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    this.p2pServiceProvider.onViewMsgPresent = true;
    loader.onDidDismiss(() => { this.p2pServiceProvider.onViewMsgPresent = false; });

    loader.present().then(() => {
      var p2pObject = {
        "username": this.username,
        "canal": "04",
        "codinstreceptor": this.getBankCode(this.bankName),
        "tlfdestino": this.code + this.phone,
        "concepto": this.concept,
        "identificacion": this.idType + this.idNumber,
        "monto": this.amount
      }
      this.p2pServiceProvider.paymentP2PRequest(p2pObject)
        .then(data => {
          this.p2pResponse = data;
          this.viewCrtl.dismiss({
            response: this.p2pResponse,
            status: true
          });
          loader.dismiss();
        });
    });
  }

  formatAmount(number) {
    return number.replace(/\./gi, ",").replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }
}



