import { HttpClientModule } from '@angular/common/http';
import { LOCALE_ID, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { NgIdleKeepaliveModule } from '@ng-idle/keepalive'; // this includes the core NgIdleModule but includes keepalive providers for easy wireup
import { MomentModule } from 'angular2-moment';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { DirectivesModule } from '../directives/directives.module';
import { AffiliationTermsPage } from '../pages/affiliation-terms/affiliation-terms';
import { AffiliationPage } from '../pages/affiliation/affiliation';
import { ChangePasswordPage } from '../pages/change-password/change-password';
import { ClientQaRequestPage } from '../pages/client-qa-request/client-qa-request';
import { ClientidRequestPage } from '../pages/clientid-request/clientid-request';
import { CompanyBalancePage } from '../pages/company-balance/company-balance';
import { CompanyReceivedPage } from '../pages/company-received/company-received';
import { FrecuentAdditionPage } from '../pages/frecuent-addition/frecuent-addition';
import { FrecuentSelectionPage } from '../pages/frecuent-selection/frecuent-selection';
import { HomePage } from '../pages/home/home';
import { OptionsPage } from '../pages/options/options';
import { OrderDetailPage } from '../pages/order-detail/order-detail';
import { PaymentOrderPage } from '../pages/payment-order/payment-order';
import { PhoneChooserPage } from '../pages/phone-chooser/phone-chooser';
import { QuestionRequestPage } from '../pages/question-request/question-request';
import { ReceivedPaymentsPage } from '../pages/received-payments/received-payments';
import { ResetPasswordPage } from '../pages/reset-password/reset-password';
import { SentPaymentsPage } from '../pages/sent-payments/sent-payments';
import { VerifyAffiliationPage } from '../pages/verify-affiliation/verify-affiliation';
import { PipesModule } from '../pipes/pipes.module';
import { P2PServiceProvider } from '../providers/p2p-service/p2p-service';
import { TimerProvider } from '../providers/timer/timer';
import { MyApp } from './app.component';

import localeEs from '@angular/common/locales/es';
import { registerLocaleData } from '@angular/common';
import { Keyboard } from '@ionic-native/keyboard';


registerLocaleData(localeEs, 'es');

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    AffiliationPage,
    AffiliationTermsPage,
    OptionsPage,
    ChangePasswordPage,
    ClientidRequestPage,
    ClientQaRequestPage,
    CompanyBalancePage,
    CompanyReceivedPage,
    PaymentOrderPage,
    PhoneChooserPage,
    ReceivedPaymentsPage,
    SentPaymentsPage,
    OrderDetailPage,
    ResetPasswordPage,
    VerifyAffiliationPage,
    QuestionRequestPage,
    FrecuentAdditionPage,
    FrecuentSelectionPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    MomentModule,
    DirectivesModule,
    PipesModule,
    NgIdleKeepaliveModule.forRoot(),
    IonicModule.forRoot(MyApp, {
      backButtonText: '',
      iconMode: 'ios',
      modalEnter: 'modal-slide-in',
      modalLeave: 'modal-slide-out',
      tabsPlacement: 'bottom',
      pageTransition: 'ios-transition'
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    AffiliationPage,
    AffiliationTermsPage,
    OptionsPage,
    ChangePasswordPage,
    ClientidRequestPage,
    ClientQaRequestPage,
    CompanyBalancePage,
    CompanyReceivedPage,
    PaymentOrderPage,
    PhoneChooserPage,
    ReceivedPaymentsPage,
    SentPaymentsPage,
    OrderDetailPage,
    ResetPasswordPage,
    VerifyAffiliationPage,
    QuestionRequestPage,
    FrecuentAdditionPage,
    FrecuentSelectionPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    { provide: LOCALE_ID, useClass: IonicErrorHandler, useValue: 'es' },
    P2PServiceProvider,
    TimerProvider
  ]
})
export class AppModule { }



