//Modules & Components
import { Component } from '@angular/core';
import { AlertController, LoadingController, ModalController, NavController, NavParams } from 'ionic-angular';

import { DateviewPipe } from '../../pipes/dateview/dateview';
import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';

@Component({
  selector: 'page-sent-payments',
  templateUrl: 'sent-payments.html',
  providers: [DateviewPipe]
})
export class SentPaymentsPage {
  public loginData: any;
  public payments: any;
  public username: any;
  public items = [];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public modalCtrl: ModalController,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider,
    private datePipe: DateviewPipe
  ) {
    this.loginData = navParams.get('loginData');
    this.username = navParams.get('username');
    this.paymentsRequest();
  }

  paymentsRequest() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    this.p2pServiceProvider.onViewMsgPresent = true;
    loader.onDidDismiss(() => { this.p2pServiceProvider.onViewMsgPresent = false; });

    loader.present().then(() => {
      var SentPaymentsObject = {
        username: this.username,
        canal: "04",
        fechahora_disp: new Date(),
        tipomov: "E"
      }

      this.p2pServiceProvider.paymentsRequest(SentPaymentsObject)
        .then(data => {
          this.payments = data;
          if (this.payments.name == null) {
            loader.dismiss();
            this.createList(this.payments);
          } else {
            loader.dismiss();
            this.navCtrl.pop();
          }
        });
    });
  }

  itemSelected(item) {
    let tranState = item.codigo_resp == "checkmark-circle-outline" ? 'Transacción Exitosa' : 'Transacción Fallida';

    let alert = this.alertCtrl.create({
      title: 'Detalle del Pago',
      message: '<b>' + tranState + '</b><hr/>' +
        '<b id="dateHead">Banco:</b> ' + item.codigo_banco + '<br/>' +
        '<b id="dateHead">Fecha:</b> ' + this.datePipe.transform(item.fecha_tran, 'DATETIME') + '<br/>' +
        '<b id="dateHead">Monto:</b> ' + this.numberWithCommas(item.monto) + ' Bs.<br/>' +
        '<b id="dateHead">Concepto:</b> ' + item.concepto + '<br/>' +
        '<b id="dateHead">Ref. Quantum:</b> ' + item.trace_quantum + '<br/>' +
        '<b id="dateHead">Ref. Suiche:</b> ' + item.codigo_suiche,
      buttons: ['CERRAR']
    });

    alert.present();
  }

  createList(payments) {

    if (payments.lista_mov != null && payments.lista_mov.length > 0) {
      this.manageBankList(payments.lista_mov)
      for (let i of payments.lista_mov) {
        if (i.codigo_resp == "00") {
          i.codigo_resp = "checkmark-circle-outline";
        } else {
          i.codigo_resp = "close-circle";
        }
        this.items.push(i);
      }

    } else {
      if (this.payments.cod_respuesta == this.p2pServiceProvider.SESSION_EXPIRED) {
        //TOKEN EXPIRED -> LOG OUT ACTION
        this.displayMessage("Error", this.payments.cod_respuesta + " - " + this.payments.descripcion_respuesta);
        this.navCtrl.popToRoot();
      }
      else {
        this.displayMessage("Aviso", "No existen movimientos registrados");
        this.navCtrl.pop();
      }
    }
  }

  manageBankList(payments) {
    for (let i of payments) {
      for (let j of this.loginData.lista_bancos) {
        if (i.codigo_banco == j.codigo) {
          i.codigo_banco = j.nombre
        }
      }
    }
    return payments;
  }

  numberWithCommas(number) {
    let aux = number;
    number = number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","); // Colocando comas en formateo de miles
    let comma = /\,/gi;
    let dot = /\./gi;
    let asterisco = /\*/gi;

    number = number.replace(dot, "-");
    number = number.replace(comma, ".");
    number = number.replace(asterisco, ".");
    number = number.replace("-", ",")

    if (aux % 1 == 0) {
      number = number + ",00"
    }

    return number;
  }

  displayMessage(title, subtitle) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subtitle,
      buttons: ['OK']
    });
    alert.present();
  }

  maskPhone(phone) {
    if (phone.length > 10) {
      phone = phone.substring(0, 4) + 'XXX' + phone.substring(7, phone.length + 1);
    }
    return phone;
  }
}



