//Modules & Components
import 'rxjs/add/operator/map';

import { Injectable } from '@angular/core';
import { DEFAULT_INTERRUPTSOURCES, Idle } from '@ng-idle/core';
import { Keepalive } from '@ng-idle/keepalive';
import { AlertController, App } from 'ionic-angular';

import { AffiliationTermsPage } from '../../pages/affiliation-terms/affiliation-terms';
import { AffiliationPage } from '../../pages/affiliation/affiliation';
import { ClientidRequestPage } from '../../pages/clientid-request/clientid-request';
import { HomePage } from '../../pages/home/home';
import { PhoneChooserPage } from '../../pages/phone-chooser/phone-chooser';
import { QuestionRequestPage } from '../../pages/question-request/question-request';
import { ResetPasswordPage } from '../../pages/reset-password/reset-password';
import { VerifyAffiliationPage } from '../../pages/verify-affiliation/verify-affiliation';
import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';

@Injectable()
export class TimerProvider {

  lastPing?: Date = null;
  timedOut = false;

  constructor(
    private idle: Idle,
    private keepalive: Keepalive,
    private alertCtrl: AlertController,
    private p2pServiceProvider: P2PServiceProvider,
    protected app: App
  ) {
    idle.setIdle(90);
    idle.setTimeout(90);
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    idle.onTimeout.subscribe(() => {
      if (!(this.app.getActiveNav().getActive().instance instanceof AffiliationPage) &&
        !(this.app.getActiveNav().getActive().instance instanceof AffiliationTermsPage) &&
        !(this.app.getActiveNav().getActive().instance instanceof ClientidRequestPage) &&
        !(this.app.getActiveNav().getActive().instance instanceof HomePage) &&
        !(this.app.getActiveNav().getActive().instance instanceof PhoneChooserPage) &&
        !(this.app.getActiveNav().getActive().instance instanceof QuestionRequestPage) &&
        !(this.app.getActiveNav().getActive().instance instanceof ResetPasswordPage) &&
        !(this.app.getActiveNav().getActive().instance instanceof VerifyAffiliationPage)) {
        this.timedOut = true;
        this.displayMessage("Aviso", "La sesión ha caducado, por favor ingrese nuevamente");
        this.app.getActiveNav().popToRoot();
        this.p2pServiceProvider.logoutRequest();
      }
      this.reset();
    });

    // sets the ping interval to 15 seconds
    this.keepalive.interval(15);

    this.keepalive.onPing.subscribe(() => this.lastPing = new Date());

    this.reset();
  }

  reset() {
    this.idle.watch();
    this.timedOut = false;
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }
}



