import { Component } from '@angular/core';
import { AlertController, ModalController, NavController, NavParams } from 'ionic-angular';
import { LoadingController } from 'ionic-angular/components/loading/loading-controller';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { AffiliationTermsPage } from '../affiliation-terms/affiliation-terms';

@Component({
  selector: 'page-affiliation',
  templateUrl: 'affiliation.html',
})
export class AffiliationPage {
  public accounts: Object;
  public clientInfo: any;
  private forbidden: any;
  private regexPsw: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public p2pServiceProvider: P2PServiceProvider,
    public modalCtrl: ModalController,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController) {

    let receiveData: any = navParams.get("clientInfo");

    this.clientInfo = {
      id: receiveData.id, userType: 'N', name: receiveData.name,
      docType: receiveData.docType, docNumber: receiveData.docNumber,
      email: receiveData.email, phone: receiveData.phone, validationKey: receiveData.validationKey,
      account: '', userName: '', psw: '', pswConfirm: '', securityKey: '', questionSec: '', questionAns: ''
    };

    this.accounts = receiveData.accounts;
    this.regexPsw = receiveData.regexPsw;
    this.forbidden = receiveData.forbidden;
  }

  displayMessage(title, subtitle, textBt) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subtitle,
      buttons: [textBt]
    });
    alert.present();
  }

  cancelAffiliation() {
    this.navCtrl.popToRoot();
  }

  showPasswordHelp() {
    this.p2pServiceProvider.showPasswordHelp(this.forbidden);
  }

  showUserNameHelp() {
    this.p2pServiceProvider.showAffiliationUsernameHelp(this.forbidden, true);
  }

  showSecurityHelp() {
    this.p2pServiceProvider.showAffiliationFieldsHelp(this.forbidden, false);
  }

  sendAffiliation() {
    if (this.clientInfo.account == "") {
      this.displayMessage("Error", "Debe seleccionar un número de cuenta.", "Cerrar");
    }
    else if (this.clientInfo.userName == "" || this.clientInfo.userName.length < 4
      || (new RegExp(/^[a-z0-9]+$/gi).test(this.clientInfo.userName)) == false
      || this.p2pServiceProvider.validateAffiliationUsername(this.clientInfo.docType + '' + this.clientInfo.docNumber,
        this.forbidden, this.clientInfo.userName) == false) {

      let alert = this.alertCtrl.create({
        title: "Error",
        subTitle: "El usuario no cumple con los requisitos.",
        buttons: [{
          text: 'OK'
        }, {
          text: 'AYUDA',
          handler: () => {
            this.showUserNameHelp();
          }
        }]
      });
      alert.present();
    }
    else if (this.clientInfo.psw == "" || this.clientInfo.pswConfirm == "") {
      this.displayMessage("Error", "Debe ingresar la clave y su confirmación", "Cerrar");
    }
    else if (this.clientInfo.questionSec == "" || this.clientInfo.questionSec.length < 4
      || (new RegExp(/^[a-z0-9\s]+$/gi).test(this.clientInfo.questionSec)) == false
      || this.p2pServiceProvider.validateAffiliationFields(this.clientInfo.name,
        this.clientInfo.docType + '' + this.clientInfo.docNumber, this.forbidden, this.clientInfo.questionSec) == false) {

      let alert = this.alertCtrl.create({
        title: "Error",
        subTitle: "La pregunta secreta no cumple con los requisitos.",
        buttons: [{
          text: 'OK'
        }, {
          text: 'AYUDA',
          handler: () => {
            this.showSecurityHelp();
          }
        }]
      });
      alert.present();
    }
    else if (this.clientInfo.questionAns == "" || this.clientInfo.questionAns.length < 4
      || (new RegExp(/^[a-z0-9\s]+$/gi).test(this.clientInfo.questionAns)) == false || this.p2pServiceProvider.validateAffiliationFields(this.clientInfo.name,
        this.clientInfo.docType + '' + this.clientInfo.docNumber, this.forbidden, this.clientInfo.questionAns) == false) {

      let alert = this.alertCtrl.create({
        title: "Error",
        subTitle: "La respuesta secreta no cumple con los requisitos.",
        buttons: [{
          text: 'OK'
        }, {
          text: 'AYUDA',
          handler: () => {
            this.showSecurityHelp();
          }
        }]
      });
      alert.present();
    }
    else if (this.clientInfo.securityKey.length == 0) {
      this.displayMessage("Error", "Debe ingresar la Clave Tu Pago Móvil (SMS).", "Cerrar");
    }
    else if (this.clientInfo.psw != this.clientInfo.pswConfirm) {
      this.displayMessage("Error", "La confirmación de la clave debe ser igual a la clave ingresada.", "Cerrar");
    }
    else if (new RegExp('[0-9]{4}').test(this.clientInfo.securityKey) == false) {
      this.displayMessage("Error", "La Clave Tu Pago Móvil (SMS) debe ser de 4 números.", "Cerrar");
    }
    else if (this.p2pServiceProvider.validatePassword(this.clientInfo.userName, this.clientInfo.name,
      this.clientInfo.docType + '' + this.clientInfo.docNumber, this.forbidden, this.regexPsw,
      this.clientInfo.psw) == false) {
      let alert = this.alertCtrl.create({
        title: "Error",
        subTitle: "La nueva contraseña no cumple con los requisitos.",
        buttons: [{
          text: 'OK'
        }, {
          text: 'AYUDA',
          handler: () => {
            this.showPasswordHelp();
          }
        }]
      });
      alert.present();
    }
    else {
      let modal = this.modalCtrl.create(AffiliationTermsPage);
      modal.onDidDismiss(resp => {
        if (resp.id == 1) {
          //ACCEPTED
          let loader = this.loadingCtrl.create({
            content: "Procesando...",
          });

          loader.present().then(() => {
            this.p2pServiceProvider.sendUserAffiliation(this.clientInfo).then(data => {
              let res: any = data;

              if (res.cod_respuesta && !isNaN(res.cod_respuesta) && parseInt(res.cod_respuesta) == 0) {
                loader.dismiss();
                this.displayMessage("Afiliación", "Usuario afiliado exitosamente", "OK");
                this.navCtrl.popToRoot();
              }
              else {
                if (res.cod_respuesta && res.descripcion_respuesta) {
                  this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta, "Cerrar");
                }
                else {
                  this.displayMessage("Error", "Error de comunicación, mensaje no disponible", "Cerrar");
                }
                loader.dismiss();
              }
            });
          });
        }
      });
      modal.present();
    }
  }

}



