import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams, Platform } from 'ionic-angular';
import { Observable } from 'rxjs/Rx';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { HomePage } from '../home/home';

@Component({
  selector: 'page-client-qa-request',
  templateUrl: 'client-qa-request.html',
})
export class ClientQaRequestPage {

  public securityCode: string;
  public securityQuestion: string;
  public securityAnswer: string;

  public countDown;
  private counter = 300;

  public securityInfo = {
    username: '',
    userId: '',
    baseRegExp: '',
    forbidden: ''
  };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public platform: Platform,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider) {
    this.navCtrl.swipeBackEnabled = false;

    this.securityCode = '';
    this.securityQuestion = '';
    this.securityAnswer = '';

    this.securityInfo.username = navParams.get('usuario');
    this.securityInfo.baseRegExp = navParams.get('expresion_regular');
    this.securityInfo.userId = navParams.get('cedula');
    this.securityInfo.forbidden = navParams.get('lista_palabras');

    if (navParams.get('isLoginCall')) this.resendValidCode();
  }

  ionViewDidLoad() {
    this.countDown = Observable.timer(0, 1000).take(this.counter)
      .map(() => this.counter = this.counter == 0 ? 0 : (this.counter % 100) == 0 ? (this.counter - 41) : (this.counter - 1));
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

  showSecurityHelp() {
    this.p2pServiceProvider.showAffiliationFieldsHelp(this.securityInfo.forbidden, false);
  }

  validateSecurityChange() {
    let validCode: boolean = false;

    if (this.securityCode && this.securityCode.length >= 8 && this.securityCode.match('^[a-zA-Z0-9]*$')) {
      validCode = true;
    }

    if (validCode) {
      if (this.securityQuestion == '' || this.securityQuestion.length < 4 ||
        (new RegExp(/^[a-z0-9\s]+$/gi).test(this.securityQuestion)) == false ||
        this.p2pServiceProvider.validateAffiliationFields(this.securityInfo.username,
          this.securityInfo.userId, this.securityInfo.forbidden, this.securityQuestion) == false) {
        this.displayMessage("Error", "La pregunta secreta no cumple con los requisitos");
      }
      else if (this.securityAnswer == '' || this.securityAnswer.length < 4 ||
        (new RegExp(/^[a-z0-9\s]+$/gi).test(this.securityAnswer)) == false ||
        this.p2pServiceProvider.validateAffiliationFields(this.securityInfo.username,
          this.securityInfo.userId, this.securityInfo.forbidden, this.securityAnswer) == false) {
        this.displayMessage("Error", "La respuesta secreta no cumple con los requisitos");
      }
      else {
        this.userSecurityChange();
      }
    }
    else {
      this.displayMessage("Error", "El código de seguridad debe solo contener letras y/o números");
    }
  }

  resendValidCode() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.userSecurityChangeRequest(this.securityInfo.username.toLowerCase())
        .then(data => {
          let userSecurityData: any = data;
          if (userSecurityData._body == null && userSecurityData.name == null && parseInt(userSecurityData.cod_respuesta) == 0) {
            loader.dismiss();
            this.counter = 300;
            this.ionViewDidLoad();
          } else {
            if (userSecurityData._body == null && userSecurityData.name == null) {
              this.displayMessage("Error", userSecurityData.cod_respuesta + ' - ' + userSecurityData.descripcion_respuesta)
            }
            loader.dismiss();
          }
        });
    });
  }

  userSecurityChange() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    loader.present().then(() => {
      this.p2pServiceProvider.userSecurityChangeQA(this.securityInfo.username.toLowerCase(), this.securityQuestion, this.securityAnswer, this.securityCode)
        .then(data => {
          let userSecurityData: any = data;
          if (userSecurityData._body == null && userSecurityData.name == null && parseInt(userSecurityData.cod_respuesta) == 0) {
            this.displayMessage("Aviso", "Operación Exitosa");
            loader.dismiss();
            this.navCtrl.setRoot(HomePage);
            this.navCtrl.popToRoot();
          } else {
            if (userSecurityData._body == null && userSecurityData.name == null) {
              this.displayMessage("Error", userSecurityData.cod_respuesta + ' - ' + userSecurityData.descripcion_respuesta);
            }
            loader.dismiss();
          }
        });
    });
  }
}



