import { Component } from '@angular/core';
import {
  AlertController,
  LoadingController,
  ModalController,
  NavController,
  NavParams,
  ViewController,
} from 'ionic-angular';

import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { FrecuentAdditionPage } from '../frecuent-addition/frecuent-addition';

@Component({
  selector: 'page-frecuent-selection',
  templateUrl: 'frecuent-selection.html',
})
export class FrecuentSelectionPage {

  public contactCero:boolean = false;
  public frecuentList: any;

  constructor(
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    public navCtrl: NavController,
    public navParams: NavParams,
    public p2pServiceProvider: P2PServiceProvider,
    public viewCtrl: ViewController) {}

  ionViewDidLoad(){
    this.searchFrecuentList();
  }

  ionViewDidLeave(){
    this.p2pServiceProvider.onViewMsgPresent = false;
  }

  getBankName(obj: any) : string {
    for(let elmt of this.p2pServiceProvider.loginData.lista_bancos) {
      if(elmt.codigo == obj.banco) {
        return elmt.nombre;
      }
    }
  }

  displayMessage(title, message) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['OK']
    });
    alert.onDidDismiss(() => {
      this.p2pServiceProvider.onViewMsgPresent = false;
    });
    alert.present();
  }

  searchFrecuentList() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    this.contactCero = false;
    this.p2pServiceProvider.onViewMsgPresent = true;

    loader.present().then(() => {
      this.p2pServiceProvider.searchFrecuentList().then(data => {
        let res: any = data;
        loader.dismiss();

        if(res.cod_respuesta && res.descripcion_respuesta) {
          if(isNaN(res.cod_respuesta) || parseInt(res.cod_respuesta) != 0) {
            //ERROR
            this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta);

            if(res.cod_respuesta == this.p2pServiceProvider.SESSION_EXPIRED) {
              this.viewCtrl.dismiss({cod_respuesta: res.cod_respuesta});
            }
          }
          else {
            //HANDLE RESPONSE
            if(!res.lista_frecuentes || res.lista_frecuentes.length == 0) {
              this.contactCero = true;
            }
            else {
              res.lista_frecuentes.forEach(element => {
                element.bancoDesc = this.getBankName(element);
              });
            }

            this.frecuentList = res.lista_frecuentes;
            this.p2pServiceProvider.onViewMsgPresent = false;
          }
        }
        else {
          this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
        }
      });
    });
  }

  selectFrecuent(item) {
    let frecuentData = {bank: item.bancoDesc, completeName: item.nombre, documentType: item.tipo, documentID: item.cedula,
      phoneCode: item.telefono.substring(0, 4), phone: item.telefono.substring(4, item.telefono.length)};
    this.viewCtrl.dismiss(frecuentData);
  }

  deleteFrecuent(item) {
    this.p2pServiceProvider.onViewMsgPresent = true;

    let alert = this.alertCtrl.create({ enableBackdropDismiss: false,
      title: "Confirmación", subTitle: "Eliminar de pagos frecuentes los siguientes datos",
      message: "<p><strong>Nombre: </strong>" + item.nombre + "</p><p><strong>Banco: </strong>" + item.bancoDesc
            + "</p><p><strong>Documento: </strong>" + item.tipo +"-"+ item.cedula
            + "</p><p><strong>Teléfono: </strong>" + item.telefono +"</p>",
      buttons: [ {text: 'NO' }, {text: 'SI', handler: () => {
          let loader = this.loadingCtrl.create({
            content: "Procesando...",
          });

          loader.present().then(() => {
            this.p2pServiceProvider.modifyFrecuentList(item, true).then(data => {
              let res: any = data;
              loader.dismiss();

              if(res.cod_respuesta && res.descripcion_respuesta) {
                if(isNaN(res.cod_respuesta) || parseInt(res.cod_respuesta) != 0) {
                  //ERROR
                  this.displayMessage("Error", res.cod_respuesta + ' - ' + res.descripcion_respuesta);

                  if(res.cod_respuesta == this.p2pServiceProvider.SESSION_EXPIRED) {
                    this.viewCtrl.dismiss({cod_respuesta: res.cod_respuesta});
                  }
                }
                else {
                  //HANDLE RESPONSE
                  let alert = this.alertCtrl.create({
                    title: "Exito",
                    subTitle: "Pago frecuente eliminado exitosamente",
                    buttons: ['OK']
                  });
                  alert.onDidDismiss(() => {
                    this.searchFrecuentList();
                  });
                  alert.present();
                }
              }
              else {
                this.displayMessage("Error", "Error de comunicación, mensaje no disponible");
              }
            });
          });
        }
      }]
    });
    alert.present();
  }

  showAddFrecuentView() {
    let modal = this.modalCtrl.create(FrecuentAdditionPage);
    modal.onDidDismiss(data => {
      let res = data;

      if(res && res.updateView == true) {
        this.searchFrecuentList();
      }
      else if(res && res.cod_respuesta && res.cod_respuesta == this.p2pServiceProvider.SESSION_EXPIRED) {
        this.viewCtrl.dismiss({cod_respuesta: res.cod_respuesta});
      }
    });
    modal.present();
  }
}



