//Modules & Components
import { Component } from '@angular/core';
import { LoadingController, NavController, NavParams } from 'ionic-angular';

import { HomePage } from '../../pages/home/home';
import { P2PServiceProvider } from '../../providers/p2p-service/p2p-service';
import { ChangePasswordPage } from '../change-password/change-password';
import { CompanyBalancePage } from '../company-balance/company-balance';
import { CompanyReceivedPage } from '../company-received/company-received';
import { PaymentOrderPage } from '../payment-order/payment-order';
import { ReceivedPaymentsPage } from '../received-payments/received-payments';
import { SentPaymentsPage } from '../sent-payments/sent-payments';

@Component({
  selector: 'page-options',
  templateUrl: 'options.html'
})
export class OptionsPage {
  public loginData: any;
  public password: any;
  public username: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public p2pServiceProvider: P2PServiceProvider
  ) {
    this.loginData = navParams.get('loginData');
    this.password = navParams.get('password');
    this.username = navParams.get('username');

    this.p2pServiceProvider.accountInfo.balance = this.formatAmount(Number(this.loginData.saldo_disponible).toFixed(2));
    this.p2pServiceProvider.accountInfo.dailyLimit = this.formatAmount(Number(this.loginData.limite_diario - this.loginData.acum_diario).toFixed(2));
    this.p2pServiceProvider.accountInfo.monthLimit = this.formatAmount(Number(this.loginData.limite_mensual - this.loginData.acum_mensual).toFixed(2));
  }

  isCompany(): boolean {
    //0: Natural 1: Company
    return this.loginData.tipo_afiliado == 1;
  }

  formatAmount(amount) {
    return amount.replace(/\./gi, ",").replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  nextPage(option) {

    var args = {
      loginData: this.loginData,
      username: this.username,
      password: this.password
    };

    switch (option) {
      case 1:
        this.navCtrl.push(PaymentOrderPage, args);
        break;
      case 2:
        this.navCtrl.push(SentPaymentsPage, args);
        break;
      case 3:
        this.navCtrl.push(ReceivedPaymentsPage, args);
        break;
      case 4:
        this.navCtrl.push(ChangePasswordPage, args);
        break;
      case 5:
        this.logout();
        break;
      case 6:
        this.navCtrl.push(CompanyBalancePage, args);
        break;
      case 7:
        this.navCtrl.push(CompanyReceivedPage, args);
        break;
    }
  }

  logout() {
    let loader = this.loadingCtrl.create({
      content: "Procesando...",
    });

    this.p2pServiceProvider.onViewMsgPresent = true;
    loader.onDidDismiss(() => { this.p2pServiceProvider.onViewMsgPresent = false; });

    loader.present().then(() => {
      this.p2pServiceProvider.logoutRequest()
        .then(data => {
          loader.dismiss();
          this.navCtrl.setRoot(HomePage);
          this.navCtrl.popToRoot();
        });
    });
  }
}



